import { format } from "date-fns"
import { WatchlistDataTablePriceCell } from "../../WatchlistDataTableCells/WatchlistDataTablePriceCell"
import { genericColumn } from "../../genericColumn"

function price(_tableName: string) {
  const title = "Latest"
  return genericColumn({
    endpoint: "companyQuote",
    columnKeys: ["price"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    extraFinanceData: entityIds => ({ companyCurrency: { entityIds } }),
    cell: info => {
      const timestamp = new Date(info.row.original.financialData.companyQuote?.data?.timestamp || 0)
      const currency = info.row.original.financialData.companyCurrency?.data?.tradingCurrency
      return (
        <WatchlistDataTablePriceCell
          queryResult={info.row.original.financialData.companyQuote}
          tooltip={format(timestamp, "MMMM d hh:mm a")}
          value={info.row.original.financialData.companyQuote?.data?.price}
          currency={currency}
        />
      )
    },
  })
}

export { price }
